import React, { createContext, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';
import SereneFinance from '../../serene-finance';
import config from '../../config';

export interface SereneFinanceContext {
  sereneFinance?: SereneFinance;
}

export const Context = createContext<SereneFinanceContext>({ sereneFinance: null });

export const SereneFinanceProvider: React.FC = ({ children }) => {
  const { ethereum, account } = useWallet();
  const [sereneFinance, setSereneFinance] = useState<SereneFinance>();

  useEffect(() => {
    if (!sereneFinance) {
      const serene = new SereneFinance(config);
      if (account) {
        // wallet was unlocked at initialization
        serene.unlockWallet(ethereum, account);
      }
      setSereneFinance(serene);
    } else if (account) {
      sereneFinance.unlockWallet(ethereum, account);
    }
  }, [account, ethereum, sereneFinance]);

  return <Context.Provider value={{ sereneFinance }}>{children}</Context.Provider>;
};
