  /**
   * @returns TokenStat for PBL
   * priceInFTM
   * priceInDollars
   * TotalSupply
   * CirculatingSupply (always equal to total supply for bonds)
   */
  async getPebbleStat(): Promise<TokenStat> {
    const { ShareRewardPool } = this.contracts;

    const supply = await this.whale.totalSupply();

    //const priceInFTM = await this.getTokenPriceFromPancakeswap(this.whale);
    const priceInFTM = await this.getTokenPriceInFish(this.whale,this.WHALE_FISH_LP);
    const lpRewardPoolSupply = await this.whale.balanceOf(ShareRewardPool.address);

    const pebbleCirculatingSupply = supply.sub(lpRewardPoolSupply);
    const priceOfOneFTM = await this.geaFishPriceFromLp();
    //const priceOfOneFTM = 0;
    const priceOfSharesInDollars = (Number(priceInFTM) * Number(priceOfOneFTM)).toFixed(2);

    return {
      tokenInFtm: priceInFTM,
      priceInDollars: priceOfSharesInDollars,
      totalSupply: getDisplayBalance(supply, this.whale.decimal, 2),
      circulatingSupply: getDisplayBalance(pebbleCirculatingSupply, this.whale.decimal, 2),
    };
  }

  async getTombStatInEstimatedTWAP(): Promise<TokenStat> {
    const { SeigniorageOracle, GenesisPool } = this.contracts;
    const expectedPrice = await SeigniorageOracle.twap(this.aFish.address, ethers.utils.parseEther('1'));

    const supply = await this.aFish.totalSupply();
    const GenesisPoolSupply = await this.aFish.balanceOf(GenesisPool.address);
    const sereneCirculatingSupply = supply.sub(GenesisPoolSupply);
    return {
      tokenInFtm: getDisplayBalance(expectedPrice),
      priceInDollars: getDisplayBalance(expectedPrice),
      totalSupply: getDisplayBalance(supply, this.aFish.decimal, 0),
      circulatingSupply: getDisplayBalance(sereneCirculatingSupply, this.aFish.decimal, 0),
    };
  }

