  /**
   * Buy bonds with cash.
   * @param amount amount of cash to purchase bonds with.
   */
  async buyBonds(amount: string | number): Promise<TransactionResponse> {
    const { Treasury } = this.contracts;
    const treasuryTombPrice = await Treasury.getTombPrice();
    return await Treasury.buyBonds(decimalToBalance(amount), treasuryTombPrice);
  }

  /**
   * Redeem bonds for cash.
   * @param amount amount of bonds to redeem.
   */
  async redeemBonds(amount: string): Promise<TransactionResponse> {
    const { Treasury } = this.contracts;
    const priceForTomb = await Treasury.getTombPrice();
    return await Treasury.redeemBonds(decimalToBalance(amount), priceForTomb);
  }

  async redeemForV2(amount: string): Promise<TransactionResponse> {
    const { swapper } = this.contracts;
    return await swapper.exchange(decimalToBalance(amount));
  }

  async getTotalValueLocked(): Promise<Number> {
    let totalValue = 0;
    for (const bankInfo of Object.values(bankDefinitions)) {
      const pool = this.contracts[bankInfo.contract];
      const token = this.externalTokens[bankInfo.depositTokenName];
      const tokenPrice = await this.getDepositTokenPriceInDollars(bankInfo.depositTokenName, token);
      const tokenAmountInPool = await token.balanceOf(pool.address);
      const value = Number(getDisplayBalance(tokenAmountInPool, token.decimal, 18)) * Number(tokenPrice);
      const poolValue = Number.isNaN(value) ? 0 : value;
      totalValue += poolValue;
    }

    const PBLPrice = (await this.getPebbleStat()).priceInDollars;
    const masonrypebbleBalanceOf = await this.whale.balanceOf(this.currentMasonry().address);
    const masonryTVL = Number(getDisplayBalance(masonrypebbleBalanceOf, this.whale.decimal)) * Number(PBLPrice);

    return totalValue + masonryTVL;
  }

  /**
   * Calculates the price of an LP token
   * Reference https://github.com/DefiDebauchery/discordpricebot/blob/4da3cdb57016df108ad2d0bb0c91cd8dd5f9d834/pricebot/pricebot.py#L150
   * @param lpToken the token under calculation
   * @param token the token pair used as reference (the other one would be FTM in most cases)
   * @param isTomb sanity check for usage of serene token or whale
   * @returns price of the LP token
   */
  async getLPTokenPrice(lpToken: ERC20, token: ERC20, isTomb: boolean): Promise<string> {
    const totalSupply = getFullDisplayBalance(await lpToken.totalSupply(), lpToken.decimal);
    //Get amount of tokenA
    const tokenSupply = getFullDisplayBalance(await token.balanceOf(lpToken.address), token.decimal);
    const stat = isTomb === true ? await this.getSereneV2Stat() : await this.getPebbleStat();
    const priceOfToken = stat.priceInDollars;
    //console.log('priceOfToken', priceOfToken, 'lpToken', lpToken, 'token', token, 'isTomb', isTomb);
    const tokenInLP = Number(tokenSupply) / Number(totalSupply);
    const tokenPrice = (Number(priceOfToken) * tokenInLP * 2) //We multiply by 2 since half the price of the lp token is the price of each piece of the pair. So twice gives the total
      .toString();
    return tokenPrice;
  }
  async getExtraLPTokenPrice(lpToken: ERC20, token1: ERC20, isWETH: Boolean): Promise<string> {
    const totalSupply = getDisplayBalance(await lpToken.totalSupply(), lpToken.decimal, 18);
    //Get amount of tokenA
    const token1Supply = getFullDisplayBalance(await token1.balanceOf(lpToken.address), token1.decimal);

    
    const priceOfToken = isWETH ? (await this.getWETHPrice()) : 1;
    //console.log('priceOfToken', priceOfToken, 'lpToken', lpToken, 'token', token, 'isTomb', isTomb);
    const tokenInLP = Number(token1Supply) / Number(totalSupply);
    const tokenPrice = (Number(priceOfToken) * tokenInLP * 2) //We multiply by 2 since half the price of the lp token is the price of each piece of the pair. So twice gives the total
      .toString();
    return tokenPrice;
  }
  async getWETHPrice(): Promise<string> {
    const ready = await this.provider.ready;
    if (!ready) return;
    const { WETH, USDC } = this.externalTokens;
    try {
      const fusdt_wftm_lp_pair = this.externalTokens['WETH-USDC-LP'];
      let ftm_amount_BN = await WETH.balanceOf(fusdt_wftm_lp_pair.address);
      let ftm_amount = Number(getFullDisplayBalance(ftm_amount_BN, WETH.decimal));
      let fusdt_amount_BN = await USDC.balanceOf(fusdt_wftm_lp_pair.address);
      let fusdt_amount = Number(getFullDisplayBalance(fusdt_amount_BN, USDC.decimal));
      return (fusdt_amount / ftm_amount).toString();
    } catch (err) {
      console.error(`Failed to fetch token price of WETH: ${err}`);
    }
  }
