  async geaFishPriceFromLp(): Promise<string> {
    const ready = await this.provider.ready;
    if (!ready) return;
    const { FISH, USDC } = this.externalTokens;
    try {
      const fusdt_wftm_lp_pair = this.externalTokens['FISH-USDC-LP'];
      let ftm_amount_BN = await FISH.balanceOf(fusdt_wftm_lp_pair.address);
      let ftm_amount = Number(getFullDisplayBalance(ftm_amount_BN, FISH.decimal));
      let fusdt_amount_BN = await USDC.balanceOf(fusdt_wftm_lp_pair.address);
      let fusdt_amount = Number(getFullDisplayBalance(fusdt_amount_BN, USDC.decimal));
      return (fusdt_amount / ftm_amount).toString();
    } catch (err) {
      console.error(`Failed to fetch token price of WFTM: ${err}`);
    }
  }

